.Project-Grid {
  display: grid;
  padding-top: 2vh;
  padding-bottom: 2vh;
  letter-spacing: 0.01em;
  align-items: center;
  color:#1d1d1d;
  align-self: center;
  align-items: start;
  align-content: start;
  grid-auto-columns: 1fr;
  grid-column-gap: 2vw;
  grid-template-columns: 1fr 1fr;
}

.Project-Title{
  padding-bottom: 7.5vh;
}

.Project-img {
  width: 100%;
  height: 25vw;
  object-fit: cover;
  opacity: 0.8;
  transition: opacity .8s ease-in-out, transform .4s ease-in-out;
}

.Project-img:hover {
opacity: 1;
}

.Project-Content {
  display: grid;
  padding-top: 2vh;
  padding-bottom: 2vh;
  letter-spacing: 0.01em;
  align-items: center;
  color:#1d1d1d;
  align-self: center;
  align-items: start;
  align-content: start;
  grid-auto-columns: 1fr;
  grid-column-gap: 2vw;
  grid-template-columns: 1fr 3fr 0.1fr;
}

.Project-Video {
  padding-bottom: 7vh;
}

.Text-Heading {
  padding-top: 1rem;
  font-style: italic;
}

.Text-Body {
  padding-top: 0.5rem;
  padding-bottom: 3rem;
  width: 90%;
}


@media only screen and (max-width: 1024px) 
and (orientation: landscape) {
  /* For tablets & mobile phones: */
  .Project-Grid {
    grid-template-columns: 0.1fr 1fr;
  }

  .Project-Content {
    grid-template-columns: 0.1fr 3fr 0.1fr;
  }

  .Project-Video {
    padding-bottom: 1.5vh;
  }

}

@media only screen and (max-width: 768px) 
and (orientation: portrait) {
  /* For tablets & mobile phones: */
  .Project-Grid {
    grid-template-columns: 0.1fr 1fr;
  }

  .Project-Content {
    grid-template-columns: 0.1fr 3fr 0.1fr;
  }

  .Project-Video {
    padding-bottom: 1.5vh;
  }

}