.Footer {
  padding-top: 30vh;
}

.Footer-Flex {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.footerItem {
  font-weight: bold;
  list-style: none;
  display: inline-block;
  padding-left: 0.8rem;
  font-size: 1.5rem;
  opacity: 0.7;
  transition: opacity 0.8s;
}

.footerItem > .icons {
  color: #1f1f1f;
}

.footerItem:hover {
  opacity: 1;
}

.To-Top {
  position: absolute;
  text-align: left;
  font-size: 0.6rem;
  font-style: italic;
  cursor: pointer;
}

@media only screen and (max-width: 768px) 
and (orientation: portrait) {
  /* For tablets & mobile phones: */
  .Footer {
    padding-top: 10vh;
  }

}