.Header {
  position: fixed;
  width: 100%;
  font-size: 0.8rem;
  background-color: rgba(243, 243, 243, 0.6);
  backdrop-filter: blur(2px);
  z-index: 5;
}

.Nav-container {
  display: grid;
  margin-top: 2.5vh;
  margin-bottom: 2vh;
  letter-spacing: 0.01em;
  align-items: center;
  color:#1d1d1d;
  align-self: center;
  align-items: start;
  align-content: start;
  grid-auto-columns: 1fr;
  grid-column-gap: 2vw;
  grid-template-columns: 1fr 25%;
  animation: fade-in 2.5s !important;
}

.Nav-name {
  font-style: italic;
}

.Menu {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  grid-gap: 2vw;
  text-transform: uppercase;
}

.Nav-link {
  color:rgb(29, 29, 29);
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.Nav-link:after {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 1px;
  bottom: 0;
  left: 0;
  background-color: #1d1d1d;
  transform-origin: bottom right;
  transition: transform 0.3s ease-out;
}

.Nav-link:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}